export default {
	components: {
		timers
	},
	data() {
		return {
			title: 'Hello',
			list: [],
		}
	},
	onLoad() {

	},
	methods: {
		alerts(e) {
			console.log(this)
		},
		routers() {
			this.search('https://www.easy-mock.com/mock/5c6a97a0b350a069f379de28/login')
		},
		stops() {
			this.$refs.timerStop.parentHandleclick(true)
		},
		search(callback, params = {}, path = true) {
			uni.request({
				url: callback,
				data: {
					params
				},
				header: {
					'custom-header': 'hello' //自定义请求头信息
				},
				success: (res) => {
					if (path !== true) {
						uni.navigateTo({
							url: path
						})
					} else {
						console.log(1)
						this.list = res.data.list
					}
				}
			})
		}
	},
}
